
.clearfix::after{content:'\20';display:block;height:0;line-height:0;visibility:hidden;clear:both
}
.hotel-content{width:1200px;margin:0 auto
}
.display-img ul,.news ul,.hotel-list{margin:0;padding:0;list-style:none
}
.display-img,.display-img li,.display-img li img{height:300px;width:740px
}
.display-img{overflow:hidden
}
.display-img ul{position:absolute;top:0
}
.display-img li{position:relative;top:0;float:left;overflow:hidden
}
.display-img li a{display:block
}
.display-img li .img_desc{display:block;position:absolute;background-color:rgba(0,0,0,0.4);bottom:-30px;width:100%;height:30px;line-height:30px;padding:0 10px 0 10px;transition:all 500ms;color:#fff
}
.display-img li:hover .img_desc{bottom:0
}
.display-img .control{display:block;position:absolute;height:80px;width:60px;line-height:80px;top:50%;margin-top:-45px;color:#a2a2a2;font-size:6em;text-align:center;transition:all 500ms;cursor:pointer;box-shadow:0 0 4px rgba(0,0,0,0.3);background-color:rgba(0,0,0,0.08);opacity:0
}
#control_right{right:76px
}
#control_left{left:76px
}
.display-img:hover .control{color:#fff;opacity:1
}
.display-img:hover #control_right{right:10px
}
.display-img:hover #control_left{left:10px
}
.news h3{margin:0;height:60px;line-height:60px;text-align:center;background-color:#32b16c;color:#fff;font-size:1.8em;letter-spacing:3px;font-weight:lighter
}
.news ul{padding:0 10px
}
.news ul li{position:relative;height:40px;width:100%;line-height:40px
}
.news ul li a{text-decoration:none;color:#7d7d7d
}
.news ul li a:hover{color:#fe005b
}
.news ul li a:hover:after{opacity:0.8
}
.news ul li h4{display:inline;max-width:260px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;margin:0;padding:0;font-size:0.9em;float:left
}
.news ul li span{float:right
}
.news ul a::after{content:'';position:absolute;bottom:0;left:0;height:2px;background-color:#fe005b;width:100%;opacity:0;transition:all 500ms
}
.hot-logo{height:92px;width:240px;position:absolute;top:14px;left:0
}
.hot-logo h3{margin:0;font-size:3.7em;text-align:center;color:#fff
}
.hot-logo h3 .red{color:#fe005b
}
.hot-logo h3::after{content:'';display:block;height:4px;width:100%;margin-top:6px;box-shadow:0 0 6px rgba(0,0,0,0.6);background-color:#fff
}
.hot-logo .title{display:block;width:100%;text-align:center;margin-top:16px;font-size:1.2em;color:#fff
}
.hot-logo span{cursor:default
}
.hotel-list{position:absolute;left:250px;top:27px;width:920px;height:90px
}
.hotel-list li{position:relative;float:left;margin-left:20px;width:210px;height:100px
}
.hotel-list li .hot_img_box,.hotel-list li .hot_img_box img{width:206px;height:96px
}
.hotel-list li .hot_img_box{text-decoration:none;color:#fff;display:block;border:2px solid #fff;transition:all 400ms;overflow:hidden
}
.hotel-list li .hot_img_box .img_info{display:block;position:absolute;height:30px;width:206px;bottom:0;line-height:30px;background-color:rgba(0,0,0,0.4);text-align:center;font-size:1.2em;transition:all 400ms;opacity:0
}
.hotel-list li .hot_img_box:hover{transform:scale(1.15);box-shadow:0 0 8px rgba(0,0,0,0.6)
}
.hotel-list li .hot_img_box:hover .img_info{bottom:35px;opacity:1
}
.hotel-content{position:relative;background:#eee;height:150px;margin:40px auto;background-color:#32b16c;box-shadow:0 0 5px #476153
}



