
.clearfix::after{content:'\20';display:block;height:0;line-height:0;visibility:hidden;clear:both
}
.header-title{width:1200px;margin:0 auto
}
.header-title h1,.header-title .metting-info p{padding:0;margin:0
}
.header-title{position:relative;height:300px;width:1280px;top:100px;color:#fff;z-index:2
}
.header-title h1{position:relative;letter-spacing:0.15em;color:#00ab88;background-image:-webkit-gradient(linear, 0 0, 0 100%, from(#1fffe7), to(#00ab88));-webkit-background-clip:text;-webkit-text-fill-color:transparent;font-weight:bold;font-size:6em
}
.header-title h1 span{position:absolute;left:0;color:transparent;text-shadow:0 0 10px #000;z-index:-1
}
.header-title .metting-info p{margin-top:10px;padding-left:20px;font-size:1.5em;color:#e2e2e2
}
.header-title .img-box{display:block;margin-top:16px;height:98px;width:186px;background-color:#6af2c4;border:2px solid #00bcd4;transition:box-shadow 400ms linear;overflow:hidden;cursor:pointer
}
.header-title .img-box img{height:98px;width:186px;transition:transform 400ms linear
}
.img-box:hover{box-shadow:0 0 13px rgba(106,142,130,0.6)
}
.img-box:hover img{transform:scale(1.2)
}



