
@import url(../css/reset.css);
.clearfix::after{content:'\20';display:block;height:0;line-height:0;visibility:hidden;clear:both
}
body{width:100%;min-width:1300px;margin:0 auto
}
@font-face{font-family:'icomoon';src:url("../../static/fonts/icomoon.eot?9hfdda");src:url("../../static/fonts/icomoon.eot?9hfdda#iefix") format("embedded-opentype"),url("../../static/fonts/icomoon.ttf?9hfdda") format("truetype"),url("../../static/fonts/icomoon.woff?9hfdda") format("woff"),url("../../static/fonts/icomoon.svg?9hfdda#icomoon") format("svg");font-weight:normal;font-style:normal
}
body{font-size:16px
}
p,h1,h2,h3{cursor:default
}
a>p,a>h1,a>h2,a>h3{cursor:inherit
}
[class^="icon-"],[class*=" icon-"]{font-family:'icomoon' !important;speak:none;font-style:normal;font-weight:normal;font-variant:normal;text-transform:none;line-height:1
}
#tjhzs_container{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#fefefe
}
.slide-fade-enter-active{transition:all .4s ease
}
.slide-fade-leave-active{transition:all 0.3s cubic-bezier(1, 0.5, 0.8, 1)
}
.slide-fade-leave-active{transform:translateX(10px);opacity:0
}
.slide-fade-enter{transform:translateX(-10px);opacity:0
}
.main-fade-enter-active{transition:all 0.4s ease
}
.main-fade-leave-active{transition:all 0.4s cubic-bezier(1, 0.5, 0.8, 1)
}
.main-fade-leave-active{opacity:0
}
.main-fade-enter{opacity:0
}



