
.clearfix::after{content:'\20';display:block;height:0;line-height:0;visibility:hidden;clear:both
}
.product-item h3{padding:0;margin:0
}
.product-item{width:300px;background-color:#fafafa;color:#868686;box-shadow:0 0 5px #ccc;text-align:center
}
.product-item:hover{box-shadow:0 0 20px #ccc
}
.product-item .product_item_img_box img.card_img,.product-item .product_item_img_box img,.product-item .product_item_img_box::after{position:absolute;top:0;left:0
}
.product-item h3{padding-top:20px;text-align:center;font-weight:bold
}
.product-item .product_item_img_box{position:relative;margin:20px;overflow:hidden;height:200px;background-size:cover
}
.product-item .product_item_img_box img.card_img,.product-item .product_item_img_box img{height:200px;width:260px;z-index:100
}
.product-item .product_item_img_box::after{content:'';width:260px;height:200px;filter:blur(3px);-webkit-filter:blur(3px);background:inherit
}
.product-item span{display:block;padding:0 20px 20px 20px;margin:0;font-size:1.2em
}
.product_list{margin:40px auto;text-align:center;color:#868686
}
.product_list h3{padding:40px 0 10px 0;margin:0;font-size:2em;color:#00bcd4
}
.product_list span{display:block;width:100%;font-size:1.2em
}
.product_list .product_note{margin:10px auto
}
.product_list .color-text{padding:20px 0;font-size:1.5em;color:#00bcd4
}



