
.clearfix::after{content:'\20';display:block;height:0;line-height:0;visibility:hidden;clear:both
}
.fade-in-enter-active{transition:all .8s ease
}
.fade-in-leave-active{transition:all 0.7s cubic-bezier(1, 0.5, 0.8, 1)
}
.fade-in-leave-active{transform:translateX(20px);opacity:0
}
.fade-in-enter{transform:translateX(-20px);opacity:0
}
.product-header{width:960px;margin:50px auto;padding-top:60px
}
.bottom-text{display:block;width:100%;margin:30px auto;text-align:center;font-size:2em;letter-spacing:5px;color:#d7d7d7
}



